.SidebarRow {
    display: flex;
    align-items: center;
    padding: 10px;
    cursor: pointer;
}
.SidebarRow:hover {
    background-color: Lightgrey;
    border-radius: 10px;
}
.SidebarRow > h4 {
    margin-left: 20px;
    font-weight: 600;
}
.SidebarRow > .MuiSvgIcon-root {
    font-size: xx-large;
    color: #2e81e4;
}
